/* src/components/Header.css */
.header {
    display: flex;
    flex-direction: column;
    align-items: center;
    border-bottom: 1px solid #333;
    margin-bottom: 20px;
    padding-bottom: 10px;
  }
  
  .header h1 {
    margin: 0;
    font-size: 2em;
  }
  
  .header nav ul {
    list-style: none;
    padding: 0;
    display: flex;
    gap: 20px;
    margin: 10px 0 0 0;
  }
  
  .header nav ul li {
    cursor: pointer;
    padding: 5px 10px;
    transition: background-color 0.3s;
  }
  
  .header nav ul li:hover {
    background-color: #333;
  }
  